/*-----Custom-Styles-----*/
	

/*-----Back to Top-----*/
#back-to-top {
	background: $primary;
	color: $white;
	position: fixed;
	bottom: 20px;
	left: 20px;
	z-index: 99;
	display: none;
	text-align: center;
	border-radius: 10px;
	-moz-border-radius: 10px;
	-webkit-border-radius: 10px;
	-o-border-radius: 10px;
	z-index: 10000;
	height: 50px;
	width: 50px;
	line-height: 45px;
	background-repeat: no-repeat;
	background-position: center;
	transition: background-color 0.1s linear;
	-moz-transition: background-color 0.1s linear;
	-webkit-transition: background-color 0.1s linear;
	-o-transition: background-color 0.1s linear;
	i {
		padding-top: 15px;
		font-size: 16px;
	}
	&:hover {
		background: $white;
		color: $primary;
		fill: $primary;
		border: 2px solid $primary;
	}
	&:hover .svg-icon{ 
		fill: $primary;
	}
}
#popup {
	position: absolute;
	width: auto;
	height: 30px;
	background: #fe6b1f;
	display: none;
	color: white;
	border-radius: 5px;
}
#copy {
	background: none;
	color: white;
	font-weight: bold;
	padding: 8px 25px;
	border: 0;
}
.countdown span:first-child {
