/*--- img container---*/
.image_outer_container {
	margin-top: auto;
	margin-bottom: auto;
	border-radius: 50%;
	position: relative;
}
.image_inner_container {
	border-radius: 50%;
	padding: 5px;
	img {
		height: 150px;
		width: 150px;
		border-radius: 50%;
		border: 5px solid white;
	}
}
.image_outer_container .green_icon {
	background-color: #4cd137;
	position: absolute;
	left: 10px;
	bottom: 10px;
	height: 30px;
	width: 30px;
	border: 5px solid white;
	border-radius: 50%;
}
.search {
	position: absolute;
	width: 320px;
	svg {
		transform: translateX(-126px);
		width: 180px;
		height: auto;
		stroke-width: 8px;
		stroke: $white;
		stroke-width: 1px;
		stroke-dashoffset: 0;
		stroke-dasharray: 64.6 206.305;
		transition: all 0.5s ease-in-out;
		margin-top: 5px;
	}
}
#drag-1, #drag-2 {
	width: 25%;
	height: 100%;
	min-height: 6.5em;
	margin: 10%;
	background-color: #29e;
	color: white;
	border-radius: 0.75em;
	padding: 4%;
	user-select: none;
	-webkit-transform: translate(0px, 0px);
	transform: translate(0px, 0px);
}
.search-inline {
	width: 100%;
	right: 0;
	padding: 33px 0;
	top: 0;
	position: absolute;
	opacity: 0;
	visibility: hidden;
	background-color: $white;
	z-index: 9;
	transition: all .5s ease-in-out;
	&.search-visible {
		opacity: 1;
		visibility: visible;
		animation: fadeInDown 0.2s ease-in-out;
	}
	button[type="submit"] {
		position: absolute;
		left: 52px;
		top: 0;
		background-color: transparent;
		border: 0px;
		top: 0;
		right: 80px;
		padding: 0px;
		cursor: pointer;
		width: 80px;
		height: 100%;
		background: $primary;
		color: $white;
	}
	.form-control {
		border: 0px;
		padding-right: 0;
		font-size: 20px;
		position: absolute;
		right: 1%;
		top: 0;
		height: 100%;
		width: 99%;
		outline: none;
		&:focus {
			box-shadow: none;
		}
	}
	.search-close {
		position: absolute;
		top: 0;
		left: 0;
		color: #616161;
		width: 80px;
		height: 100%;
		text-align: center;
		display: table;
		background: #efefef;
		text-decoration: none;
		i {
			display: table-cell;
			vertical-align: middle;
		}
	}
}
.searching i {
	font-size: 18px;
}
.search-close i {
	padding: 24px 0;
}
@keyframes fadeInDown {
	from {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}
.lockscreen {
	background: inherit;
	width: 300px;
	height: 280px;
	overflow: hidden;
	&:before {
		content: '';
		width: 350px;
		height: 400px;
		background: inherit;
		position: absolute;
		right: -25px;
		left: 0;
		top: -25px;
		bottom: 0;
		box-shadow: inset 0 0 0 200px $white-4;
		filter: blur(1px);
	}
}
.table-stats table {
	th, td {
		border: none;
		border-bottom: 1px solid #e8e9ef;
		font-size: 12px;
		font-weight: normal;
		padding: .75em 1.25em;
		text-transform: uppercase;
	}
	th img, td img {
		margin-left: 10px;
		max-width: 45px;
	}
	th .name {
		font-size: 14px;
		text-transform: capitalize;
	}
	td {
		.name {
			font-size: 14px;
			text-transform: capitalize;
		}
		font-size: 14px;
		text-transform: capitalize;
		vertical-align: middle;
	}
}
.traffic-sales-content {
	padding: 14px;
}
.traffic-sales-amount {
	float: left;
}
.bg-dot {
    display: block;
    position: absolute;
    top: .6rem;
    left: .9rem;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #f72d66;
    cursor: pointer;
}
.pulse {
    display: block;
    position: absolute;
    top: .1rem;
    left: .7rem;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #f72d66;
    cursor: pointer;
    border: 2px solid #eee6e6;
    box-shadow: 0 0 0 rgba(247, 45, 102, 0.9);
    animation: pulse 2s infinite;
	&:hover {
		animation: none;
	}
}
@-webkit-keyframes pulse {
	0% {
		-webkit-box-shadow: 0 0 0 0 rgba(247, 45, 102, 0.9);
	}
	70% {
		-webkit-box-shadow: 0 0 0 10px rgba(247, 45, 102, 0);
	}
	100% {
		-webkit-box-shadow: 0 0 0 0 rgba(247, 45, 102, 0);
	}
}
@keyframes pulse {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba(247, 45, 102, 0.9);
		box-shadow: 0 0 0 0 rgba(247, 45, 102, 0.7);
	}
	70% {
		-moz-box-shadow: 0 0 0 10px rgba(247, 45, 102, 0);
		box-shadow: 0 0 0 10px rgba(247, 45, 102, 0);
	}
	100% {
		-moz-box-shadow: 0 0 0 0 rgba(247, 45, 102, 0);
		box-shadow: 0 0 0 0 rgba(247, 45, 102, 0);
	}
}
