/*latetes news timeline */

.latest-timeline ul.timeline {
	list-style-type: none;
	position: relative;
	&:before {
		content: ' ';
		background: #e9ebfa;
		display: inline-block;
		position: absolute;
		right: 15px;
		width: 3px;
		height: 100%;
		z-index: 1;
	}
	>li {
		margin: 20px 0;
		padding-left: 3rem;
		&:before {
			content: ' ';
			background: white;
			display: inline-block;
			position: absolute;
			border-radius: 50%;
			border: 3px solid #1753fc;
			right: 6px;
			width: 20px;
			height: 20px;
			z-index: 1;
		}
		&:first-child:before {
			border: 3px solid #0dd157;
		}
		&:nth-child(2):before {
			border: 3px solid #2d9eef;
		}
		&:nth-child(3):before {
			border: 3px solid #f35e90;
		}
		&:nth-child(4):before {
			border: 3px solid $orange;
		}
		&:last-child:before {
			border: 3px solid #ec2d38;
		}
	}
}
