.header-search {
	min-width: 18rem !important;

	.search-element {
		position: relative;
		margin-right: 15px;
	}

	.btn {
		position: absolute;
		left: 0;
		top: 0px;
		color: #8499c4;
	}
}
.horizontal-main {
	width: 100%;
	position: relative;
}
.horizontalMenu {
	html,
	body,
	iframe,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 0;
		padding: 0;
		border: 0;
		font: inherit;
		vertical-align: baseline;
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		-webkit-text-size-adjust: 100%;
		-ms-text-size-adjust: 100%;
		font-weight: 500 !important;
		color: $color;
	}

	.cl {
		clear: both;
	}

	img {
		border: 0 none;
		max-width: 100%;
	}
}

object,
embed,
video {
	border: 0 none;
	max-width: 100%;
}

.horizontalMenu {
	a:focus {
		outline: none;
	}

	&:before {
		content: "";
		display: table;
	}

	&:after {
		content: "";
		display: table;
		clear: both;
	}
}

/* ================== Desktop Main Menu CSS ================== */

.horizontalMenucontainer {
	background-size: cover;
	overflow: hidden;
	background-attachment: fixed;
	background-position: 50% 0;
	background-repeat: no-repeat;
}

.horizontal-mainwrapper {
	margin: 0 auto;
}

.desktoplogo {
	padding: 0.75rem;
	margin: 0;
	float: right;

	img {
		width: 100%;
	}
}

.horizontalMenu {
	color: $white;
	font-size: 14px;
	padding: 0;
	display: block;

	> .horizontalMenu-list {
		text-align: right;
		margin: 0 auto 0 auto;
		width: 100%;
		display: block;
		padding: 0;

		> li {
			text-align: center;
			display: block;
			padding: 0;
			margin: 0;
			float: right;

			> a {
				display: block;
				padding: 8px 18px 8px 18px;
				text-decoration: none;
				position: relative;
				color: $color;
				font-weight: 500;
				font-size: 14px;
				margin: 0 2px;
				vertical-align: middle;
			}

			&:last-child > a {
				border-left: 0px;
				margin-left: 0;
			}

			&:first-child > a {
				margin-right: 0;
			}

			a.menuhomeicon {
				padding-right: 25px;
				padding-left: 25px;
			}

			> a {
				i.horizontal-icon {
					display: inline-block;
					font-size: 13px;
					line-height: inherit;
					margin-right: 3px;
					vertical-align: middle;
				}

				.arrow:after {
					border-right: 4px solid rgba(0, 0, 0, 0);
					border-left: 4px solid rgba(0, 0, 0, 0);
					border-top: 4px solid;
					content: "";
					float: left;
					left: 11px;
					height: 0;
					margin: 0 0 0 14px;
					position: absolute;
					text-align: left;
					top: 33px;
					width: 0;
				}
			}

			&.leftmenu {
				float: left;
				float: left;
				zoom: 1;

				a {
					padding: 0px 20px 0px 30px;
					border-left: none;

					i {
						font-size: 15px;
					}
				}

				&:before {
					content: "";
					display: table;
				}
			}
		}
	}
}

/* Desktop Search Bar */

.horizontalMenu-list > li.leftmenu:after {
	content: "";
	display: table;
}

.horizontalMenu > .horizontalMenu-list > li {
	&.leftmenu {
		&:after {
			clear: both;
		}

		> .topmenusearch {
			float: left;
			width: 210px;
			height: 39px;
			position: relative;
			margin: 16px 0px 0px 0px;

			.searchicon {
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;
			}

			input {
				width: 100%;
				position: relative;
				float: left;
				top: 0;
				left: 0;
				bottom: 0;
				width: 100%;
				border: 0;
				padding: 0;
				margin: 0;
				text-indent: 15px;
				height: 39px;
				z-index: 2;
				outline: none;
				color: #333;
				background-color: #efefef;
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;
				font-size: 12px;

				&::placeholder {
					color: #a9a9a9;
				}

				&:focus {
					color: #333;
					width: 230px;

					~ {
						.btnstyle .fa {
							color: $black;
							opacity: 1;
						}

						.searchicon {
							opacity: 1;
							z-index: 3;
							color: $white;
						}
					}
				}
			}

			.btnstyle {
				top: 0px;
				position: absolute;
				left: 0;
				bottom: 0;
				width: 40px;
				line-height: 30px;
				z-index: 1;
				cursor: pointer;
				opacity: 0.3;
				color: #333;
				z-index: 1000;
				background-color: transparent;
				border: solid 0px;
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;

				.fa {
					line-height: 37px;
					margin: 0;
					padding: 0;
					text-align: center;
				}

				&:hover .fa {
					opacity: 1;
				}
			}
		}
	}

	> {
		ul.sub-menu {
			position: absolute;
			top: 45px;
			z-index: 10;
			margin: 0px;
			padding: 10px 15px 10px 0px;
			min-width: 220px;
			background: $white;
			border: $border;
			border-radius: 4px;
			box-shadow: 0px 16px 18px rgba(6, 10, 48, .1);

			> li {
				position: relative;
				margin: 0px;
				padding: 0px;
				display: block;

				> {
					a {
						background-image: none;
						color: #576482;
						border-left: 0 none;
						text-align: right;
						display: block;
						line-height: 22px;
						padding: 6px 14px;
						text-transform: none;
						font-size: 13px;
						letter-spacing: normal;
						border-left: 0px solid;
						position: relative;

						&:hover {
							text-decoration: none;
						}

						> i {
							margin-left: 9px;
						}
					}

					ul.sub-menu {
						min-width: 220px;
						position: absolute;
						right: 100%;
						top: 0;
						margin: 0;
						padding: 10px 15px 10px 0px;
						list-style: none;
						background-color: $white;
						border:$border;
						box-shadow:0px 16px 18px rgba(6, 10, 48, .1);
						border-radius: 0 4px 4px 0;

						> li {
							position: relative;
							margin: 0px;
							padding: 0px;
							display: block;

							> {
								a {
									background-image: none;
									color: #576482;
									border-left: 0 none;
									text-align: right;
									display: block;
									line-height: 22px;
									padding: 6px 7px;
									text-transform: none;
									font-size: 13px;
									letter-spacing: normal;
									border-left: 0px solid;
									margin-right: 8px;

									&:hover {
										text-decoration: none;
									}

									> i {
										margin-left: 9px;
									}
								}

								ul.sub-menu {
									min-width: 220px;
									position: absolute;
									right: 100%;
									top: 0;
									margin: 0px;
									list-style: none;
									padding: 10px 15px 10px 0px;
									background-color: $white;
									border:$border;
									box-shadow:0px 16px 18px rgba(6, 10, 48, .1);
									border-radius: 0 4px 4px 0;

									> li {
										position: relative;
										margin: 0px;
										padding: 0px;
										display: block;

										> a {
											background-image: none;
											color: #576482;
											border-left: 0 none;
											text-align: right;
											display: block;
											line-height: 22px;
											padding: 8px 12px;
											text-transform: none;
											font-size: 13px;
											letter-spacing: normal;
											border-left: 0px solid;

											&:hover {
												text-decoration: none;
											}

											> i {
												margin-left: 9px;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}

		.horizontal-megamenu {
			width: 100%;
			right: 0px;
			position: absolute;
			top: 45px;
			color: #576482;
			z-index: 1000;
			margin: 0px;
			text-align: right;
			font-size: 15px;

			.megamenu-content {
				border: 1px solid #e8ebf3;
				background-color: $white;
				padding: 14px 20px;
				border-radius: 2px;
				box-shadow: 0px 5px 25px rgba(227, 228, 238, 0.2);
			}

			.title {
				padding: 5px 5px 5px 0px;
				font-size: 16px;
				color: #2c2c2c;
				margin: 0px 0px 4px 0px;
				text-align: right;
				font-weight: 600;
			}

			.wsmwnutxt {
				width: 100%;
				font-size: 14px;
				text-align: justify;
				line-height: 22px;
				color: #2c2c2c;
				margin-top: 10px;
			}

			.link-list li {
				display: block;
				text-align: center;
				white-space: nowrap;
				text-align: right;
				padding: 5px 10px 5px 10px;

				a {
					line-height: 18px;
					border-left: none;
					text-align: right;
					padding: 6px 7px;
					background: transparent;
					background-image: none;
					color: #576482;
					border-left: 0 none;
					display: block;
					font-size: 13px;
					margin-right: 10px;
					position: relative;
				}
			}

			li {
				i {
					margin-left: 5px;
					text-align: center;
					width: 18px;
				}

				a:hover {
					background: transparent;
					text-decoration: none;
				}
			}

			.link-list li i {
				font-size: 12px;
			}

			.mrgtop {
				margin-top: 15px;
			}

			.show-grid div {
				padding-bottom: 10px;
				padding-top: 10px;
				background-color: #dbdbdb;
				border: 1px solid #e7e7e7;
				color: #6a6a6a;
				margin: 2px 0px;
				font-size: 13px;
			}

			&.halfmenu {
				width: 30%;
				left: auto;
				right: auto;
			}

			&.halfdiv {
				width: 20%;
				left: auto;
				right: auto;
			}

			.menu_form {
				width: 100%;
				display: block;

				input[type="text"] {
					width: 100%;
					border: 1px solid #e2e2e2;
					color: $black;
					font-size: 13px;
					padding: 8px 5px;
					margin-bottom: 8px;
				}

				textarea {
					width: 100%;
					border: 1px solid #e2e2e2;
					color: $black;
					font-size: 13px;
					padding: 8px 5px;
					margin-bottom: 8px;
					min-height: 122px;
				}

				input {
					&[type="submit"] {
						width: 25%;
						display: block;
						height: 32px;
						float: left;
						border: none;
						margin-left: 15px;
						cursor: pointer;
						background-color: #e1e1e1;
						-webkit-border-radius: 2px;
						-moz-border-radius: 2px;
						border-radius: 2px;
					}

					&[type="button"] {
						width: 25%;
						display: block;
						height: 32px;
						float: left;
						border: none;
						cursor: pointer;
						background-color: #e1e1e1;
						-webkit-border-radius: 2px;
						-moz-border-radius: 2px;
						border-radius: 2px;
					}
				}
			}

			.carousel-inner .item img {
				width: 100%;
			}

			.carousel-control-next,
			.carousel-control-prev {
				opacity: 0.8;
			}

			.carousel-caption {
				bottom: 0px;
				background-color: rgba(0, 0, 0, 0.7);
				font-size: 13px;
				height: 31px;
				right: 0;
				padding: 7px 0;
				left: 0;
				width: 100%;
			}

			.typography-text {
				padding: 0px 0px;
				font-size: 14px;

				ul {
					padding: 0px 0px;
					margin: 0px;
				}

				p {
					text-align: justify;
					line-height: 24px;
					color: #656565;
				}

				ul li {
					display: block;
					padding: 2px 0px;
					line-height: 22px;

					a {
						color: #656565;
					}
				}
			}
		}
	}
}

